import { Component, ElementRef, Input, OnChanges, ViewChild, ViewEncapsulation, OnInit, AfterViewInit} from '@angular/core';
import * as d3 from 'd3';
import * as CanvasJS from '../../../assets/canvasjs.min';
import { SubjectSubscriber } from '../../../../node_modules/rxjs/internal/Subject';
import { CollegeGraphDataService} from '../../services/collegegraphdata.service';

@Component({
  selector: 'app-college-compare-graph',
  templateUrl: './college-compare-graph.component.html',
  styleUrls: ['./college-compare-graph.component.css']
})
export class CollegeCompareGraphComponent implements OnInit {

  @ViewChild('barChart')
  private chartContainer: ElementRef;

 
  dataCollege1 = [];
  college1Detail:any;
  GData1Marks:any;

  dataCollege2 = [];
  college2Detail:any;
  GData2Marks:any;
  
  margin = { top: 20, right: 20, bottom: 30, left: 40 };

  constructor(private GraphSerivce: CollegeGraphDataService) { }

  ngOnInit() {
    this.GraphSerivce.Graphdata.subscribe((data)=>{
      console.log(data)
      this.dataCollege1=data.Gdata.College1.slice(1,data.Gdata.College1.length+1);
      this.GData1Marks=[];
      for(let i=0;i<this.dataCollege1.length;i++){
        this.GData1Marks.push({
          y: this.dataCollege1[i].InternalMarks+this.dataCollege1[i].ExternalMarks, 
          name: this.dataCollege1[i].SubjectName
        })
      }


      this.college1Detail=data.Gdata.College1.slice(0,1);

      this.dataCollege2=data.Gdata.college2.slice(1,data.Gdata.college2.length+1);
      this.GData2Marks=[];
      for(let i=0;i<this.dataCollege2.length;i++){
        this.GData2Marks.push({
          y: this.dataCollege2[i].InternalMarks+this.dataCollege2[i].ExternalMarks, 
          name: this.dataCollege2[i].SubjectName
        })
      }

      this.college2Detail=data.Gdata.college2.slice(0,1);
      this.canvas();
   });
   //console.log(this.collegeDetail[0].collegeDetail)
    
   }

  canvas(){
   console.log("hello") 
    let chart = new CanvasJS.Chart("chartContainer", {
      theme: "light2",
      animationEnabled: true,
      title:{
        text: "Marks"
      },	
      legend: {
        cursor:"pointer",
        verticalAlign: "top",
        fontSize: 22,
        fontColor: "dimGrey"
      },
      data: [{
        type: "line",
        showInLegend: true,
        name:this.college1Detail[0].College,
        toolTipContent: "<b>{name}</b>: {y} (#percent%)",
        dataPoints: this.GData1Marks
      },
      {
        type: "line",
        showInLegend: true,
        name:this.college2Detail[0].College,
        toolTipContent: "<b>{name}</b>: {y} (#percent%)",
        dataPoints: this.GData2Marks
      }]
    });
      
    chart.render();
}

}
