import { Component, OnInit } from '@angular/core';
import { StudentService } from 'src/app/services/student.service';
import { Router } from '@angular/router';
import {Location} from '@angular/common';

@Component({
  selector: 'app-compare-student',
  templateUrl: './compare-student.component.html',
  styleUrls: ['./compare-student.component.css']
})
export class CompareStudentComponent implements OnInit {

  studentResultWantToCompare = [];
  resultTocompareWith = [];
  studentComaparingWith;
  studentWhomToCompare;
  marksDifference = [];
  aggregateDifference;
  constructor(private studenService: StudentService, private router: Router,private _location: Location) { }

  close() {
    this._location.back();

   // this.router.navigate(['studentResult/collegeRank']);
  }

  calculateMarksDifference() {
    for (let [i, subject] of this.resultTocompareWith.entries()) {
      let internal = parseInt(subject.subjectinternalM) - parseInt(this.studentResultWantToCompare[i].subjectinternalM);
      let external = parseInt(subject.subjectexternalM) - parseInt(this.studentResultWantToCompare[i].subjectexternalM);
      let total = parseInt(subject.subjectexternalm) - parseInt(this.studentResultWantToCompare[i].subjectexternalm);

      this.marksDifference.push({
        internal: internal,
        external: external,
        total: total
      });
    }
    this.aggregateDifference = parseFloat(this.studentComaparingWith.percentage) - parseFloat(this.studentWhomToCompare.percentage);
  }
  ngOnInit(): void {
    this.studenService.sendDataToCompareStudent.subscribe((data) => {
      this.studentResultWantToCompare = data.studentResultWantToCompare;
      this.resultTocompareWith = data.resultTocompareWith;
      this.studentComaparingWith = data.studentComaparingWith;
      this.studentWhomToCompare = data.studentWhomToCompare;
      this.calculateMarksDifference();
    })
  }

}
