import { Component, OnInit } from '@angular/core';
import { UniversityService } from 'src/app/services/university.service';
import { StudentService } from 'src/app/services/student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-university-wise-rank',
  templateUrl: './university-wise-rank.component.html',
  styleUrls: ['./university-wise-rank.component.css']
})
export class UniversityWiseRankComponent implements OnInit {

  students=[];
  semNo;
  roll;
  bi=-1;
  constructor(private universityService:UniversityService,private studentService:StudentService,private router:Router) { }

  findResult(enrollment,i){
    this.bi=i;
    this.studentService.getStudentResult({enrollment:enrollment}).subscribe((result) => {
      this.studentService.studentResult = result;
      this.studentService.noOfBacklogs=result['BACKLOGS'];
      this.studentService.Backlogs=result['SEMBACK'];
      this.router.navigate(['studentResult']);
    }, (err) => {
      console.log(err);
    });
  }
  ngOnInit(): void {
    this.universityService.universityRankInfo.subscribe((data)=>{
      this.students=data.students;
    this.semNo=data.SemNo;
    console.log(this.students);
    });
   
  }

}
