import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ConfigService } from './config.service';
import { Observable } from 'rxjs';
import { Subject, ReplaySubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})


export class CollegeGraphDataService {

  path: string = this.configService.path;
  Graphdata:ReplaySubject<any> = new ReplaySubject(1);;
  getData:any;


constructor(private configService: ConfigService,private http: HttpClient, private router: Router) { }


getCollegeAvgMarks(collegedata){
  return this.http.post(this.path + '/CollegeAverageMarks', collegedata)
}

getCollegeCompareAvgMarks(collegedata){
  return this.http.post(this.path + '/CompareCollegeAverageMarks', collegedata)
}

}
