import { Component, OnInit } from '@angular/core';
import { CollegeService } from 'src/app/services/college.service';
import { StudentService } from 'src/app/services/student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-college-wise-rank',
  templateUrl: './college-wise-rank.component.html',
  styleUrls: ['./college-wise-rank.component.css']
})
export class CollegeWiseRankComponent implements OnInit {

  students=[];
  semNo;
  roll;
  bi=-1;
  constructor(private collegeService:CollegeService,private studentService:StudentService, private router:Router) { }

  findResult(enrollment,i){
    this.bi=i;
    this.studentService.getStudentResult({enrollment:enrollment}).subscribe((result) => {
      this.studentService.studentResult = result;
      this.studentService.noOfBacklogs=result['BACKLOGS'];
      this.studentService.Backlogs=result['SEMBACK'];
      this.router.navigate(['studentResult']);
    }, (err) => {
      console.log(err);
    });
  }

  ngOnInit(): void {
     this.collegeService.clgStudentsRanks.subscribe((data)=>{
        this.students=data.students;
        this.semNo=data.SemNo;
        console.log(this.students);
     });
  }

}
