import { Injectable } from '@angular/core';
import { ConfigService } from './config.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Subject, ReplaySubject } from 'rxjs';

@Injectable()

export class CollegeService {
    path: string = this.configService.path;

    studentSem: string;
    clgStudentsRanks:ReplaySubject<any> = new ReplaySubject(1);

    constructor(private http: HttpClient, private router: Router, private configService: ConfigService) { }

    getCollegeRanks(collegeData) {
        return this.http.post(this.path + '/collegewiseranks', collegeData);
    }

    getCollegeDetails() {
        return this.http.get(this.path + '/getcollegedetails');
    }

    getProgrammmeDetails() {
        return this.http.get(this.path + '/getprogrammedetails');
    }

}