import { Component, ElementRef, Input, OnChanges, ViewChild, ViewEncapsulation, OnInit, AfterViewInit} from '@angular/core';
import * as d3 from 'd3';
import * as CanvasJS from '../../../assets/canvasjs.min';
import { SubjectSubscriber } from '../../../../node_modules/rxjs/internal/Subject';
import { CollegeGraphDataService } from '../../services/collegegraphdata.service';

@Component({
  selector: 'app-college-graph',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './college-graph.component.html',
  styleUrls: ['./college-graph.component.css'],
})
export class CollegeGraphComponent implements OnInit {
  @ViewChild('barChart')
  private chartContainer: ElementRef;

 
  data = [];
  collegeDetail:any;
  GDataInternal:any;
  GDataExternal:any;
  margin = { top: 20, right: 20, bottom: 30, left: 40 };

  constructor(private GraphSerivce: CollegeGraphDataService) { }

  ngOnInit() {
    this.GraphSerivce.Graphdata.subscribe((data)=>{
      this.data=data.Gdata.slice(1,data.Gdata.length+1);
      this.GDataInternal=[];
      this.GDataExternal=[];
      for(let i=0;i<this.data.length;i++){
        this.GDataInternal.push({
          y: this.data[i].InternalMarks, 
          name: this.data[i].SubjectName
        })
        this.GDataExternal.push({
          y: this.data[i].ExternalMarks, 
          name: this.data[i].SubjectName
        })
      }
      this.collegeDetail=data.Gdata.slice(0,1);
      this.canvas();
   });
   //console.log(this.collegeDetail[0].collegeDetail)
    
   }

  canvas(){
   console.log("hello") 
    let chart = new CanvasJS.Chart("chartContainer", {
      theme: "light2",
      animationEnabled: true,
      title:{
        text: "Marks"
      },	
      legend: {
        cursor:"pointer",
        verticalAlign: "top",
        fontSize: 22,
        fontColor: "dimGrey"
      },
      data: [{
        type: "line",
        showInLegend: true,
        name:"Internal Marks",
        toolTipContent: "<b>{name}</b>: {y} (#percent%)",
        dataPoints: this.GDataInternal
      },
      {
        type: "line",
        showInLegend: true,
        name:"External Marks",
        toolTipContent: "<b>{name}</b>: {y} (#percent%)",
        dataPoints: this.GDataExternal
      }]
    });
      
    chart.render();
}
}
