import { Component, OnInit } from '@angular/core';
import { StudentService } from 'src/app/services/student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-college-rank',
  templateUrl: './college-rank.component.html',
  styleUrls: ['./college-rank.component.css']
})
export class CollegeRankComponent implements OnInit {

  students = [];
  semNo;
  bi=-1;
  loader=false;
  studentEnrollmentNo;

  studentResultWantToCompare;
  resultTocompareWith;
  constructor(private studentService: StudentService, private router: Router) { }

  getcompareResultData(student,i) {
    if (this.studentEnrollmentNo != student.enrollmentnumber) {
      this.bi=i;

      let studentWhomToCompare;
      for(let stud of this.students){
        if(stud.enrollmentnumber==this.studentEnrollmentNo){
          studentWhomToCompare=stud;
          break;
        }
      }

      this.studentService.getResultOfStudentToCompare({ enrollment: student.enrollmentnumber }).subscribe((result) => {

        this.resultTocompareWith = result['SEMDATA'][this.studentService.studentSem]['a1'];
        this.studentService.sendDataToCompareStudent.next(
          {
            studentResultWantToCompare: this.studentResultWantToCompare,
            resultTocompareWith: this.resultTocompareWith,
            studentComaparingWith:student,
            studentWhomToCompare:studentWhomToCompare
          });
        this.router.navigate(['studentResult/compareResult']);
      });
    }
  }

  ngOnInit(): void {
    this.students = this.studentService.collegeStudentsRankInfo;
    this.semNo = this.studentService.studentSem;
    this.studentEnrollmentNo = this.studentService.studentResult.DOCS.enrollmentnumber;
    this.studentResultWantToCompare = this.studentService.studentResult['SEMDATA'][this.studentService.studentSem]['a1'];
    console.log(this.students);

  }

}
